<template>
	<view class="content" style="margin-bottom: 50px;">
		<view class="input-row" style="color:#3F536E;line-height: 20px;font-size: 15px;background-color:#B2B2B2;">
			<text style="width: 50%;">课程名称</text>
			<text style="width: 20%;">学习时长</text>
			<text style="width: 30%;">总时长</text>
		</view>
		<view class="input-row border" style="color: grey;line-height: 20px;margin-top: 10px;font-size: small;" v-for="(n, i) in studyProgress" v-bind:key="i">
			<text style="width: 50%;">{{studyProgress[i].courseName}}</text>
			<text style="width: 20%;">{{Math.floor(studyProgress[i].learnTotalTime/3600) +':'+Math.floor((studyProgress[i].learnTotalTime%3600)/60) +':'+ studyProgress[i].learnTotalTime%60}}</text>
			<text style="width: 30%;">{{Math.floor(studyProgress[i].totalTime/3600) +':'+Math.floor((studyProgress[i].totalTime%3600)/60) +':'+ studyProgress[i].totalTime%60}}</text>
		</view>
	</view>
</template> 


<script>
	import lineText from '../../../components/line-text.vue'
	export default {
		components:{
			lineText
		},
		data() {
			return {
				studyProgress:uni.getStorageSync("studyProgress")
			}
		},
		onPullDownRefresh() {
			this.loadJs.reloadStudyProgress(this.$store.baseInfoId);
			this.studyProgress = uni.getStorageSync("studyProgress");
			uni.stopPullDownRefresh();
		},
		async onReady() {
			if(!uni.getStorageSync("studyProgress")){
				await this.loadJs.reloadStudyProgress(this.$store.baseInfoId);
			}
			this.studyProgress = uni.getStorageSync("studyProgress");
		}
		
	}
</script>

